#leaveCart {
    display: flex;
    margin-bottom: 1.5rem;
}

#leaveCart > * {
    margin-right: 1rem;
}

#backToShop {
    height: 2rem;
    width: 11rem;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    position: relative;
}

#backToShop > p {
    position: absolute;
    top: -50%;
    left: 20%;
}

#totalSale {
    font-size: 2rem;
}

#confirm {
    height: 2rem;
    width: 12rem;
    font-size: 1.2rem;
}