img {
    height: 6rem;
    width: 6rem;
}

.items {
    height: 18rem;
    width: 21rem;
    display: flex;
    flex-direction: column;
    background-color: white;
    border: 3px solid black;
    margin: 1rem;
    margin-right: 5rem;
    padding-bottom: 1rem;
    align-items: center;
    box-sizing: border-box;
    position: relative;
}

.inputDiv {
    display: flex;
    height: 20%;
    width: 85%;
    position: absolute;
    bottom: 0;
}

.inputDiv > input {
    height: 50%;
    width: 35%;
    margin-right: 1rem;
    font-size: 1.5rem;
}


.testCounter {
    display: none;
}

.addCart {
    transform: scaleX(-1);
    height: 100%;
    width: auto;
    margin-right: 0.4rem;
}

.addButton {
    height: 50%;
    width: 54%;
    display: flex;
    align-items: center;
    font-size: 1rem;
}