#cartIcon {
    position: fixed;
    top: 0;
    right: 0;
    background-color: white;
    border-radius: 50%;
    height: 6rem;
    width: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.3rem solid black;
    z-index: 1;
}

#nav {
    height: 90%;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#nav > img {
    height: 80%;
    width: 80%;
}

#saleQty {
    position: fixed;
    right: 5%;
    top: 6.3%;
    color: rgb(197, 4, 197);
}