* {
    box-sizing: border-box;
}

.itemTags {
    display: flex;
    align-items: center;
    background-color: white;
    height: 10rem;
    width: 95vw;
    box-sizing: border-box;
    margin-bottom: 1rem;
    border: 0.3rem solid black;
    position: relative;
}

.priceAndQty {
    display: flex;
    align-items: center;
    font-size: 3rem;
    position: absolute;
    left: 50%;
    top: 4%;
}

.priceAndQty > * {
margin-right: 3rem;
}

.tagImages {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80%;
}

.xButtons {
    background-color: red;
    color: white;
    position: absolute;
    right: 0px;
    top: 0px;
    height: 30%;
    width: 4%;
    font-size: 2rem;
}

.incButton {
    position: absolute;
    left: 60%;
    top: 0;
    height: 20%;
    width: 10%;
    background-color: rgb(0, 255, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.decButton {
    position: absolute;
    left: 60%;
    bottom: 0;
    width: 10%;
    height: 20%;
    background-color: red;
    font-size: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.decButton > p {
    position: absolute; 
    bottom: -230%;
}

.checkoutHeading {
    font-size: 2rem;
    position: absolute;
    left: 3%;
}

.checkoutImg {
    height: 80%;
    width: auto;
    position: absolute;
    left: 22%;
}