* {
    z-index: 0;
}

#itemDisplay {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    top: 10rem;
}

body {
    background-color: rgb(255, 255, 180);
}

#shopHeading {
    text-decoration: underline;
    position: absolute;
    left: 40%;
}

#shopDiv {
    position: relative;
}

.homeButton {
    height: 2rem;
    width: 6.5rem;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    position: relative;
}

.homeButton > p {
position: absolute;
left: 33%;
top: -0.95rem;
}

.homeIcon {
    height: 90%;
    width: auto;
    position: absolute;
    bottom: 8%;
}